* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html {
  scroll-snap-type: x mandatory;
}
header {
  width: 100%;
  background-color: #000;
  padding: 10px;
  color: #fff;
}

nav {
  width: 100%;
  max-width: 1300px;
  margin: auto;
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 10px;
  border-bottom: 1px solid #fff;
}
nav h3 {
  font-size: 2rem;
}

nav > :nth-child(3) {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  font-size: 2rem;
}
nav > :nth-child(3) > :last-child {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.icons svg:hover {
  cursor: pointer;
  color: #d01418;
}
nav > :nth-child(3) > :last-child > :last-child {
  position: absolute;
  font-size: 1rem;
  top: -20%;
  right: -20%;
  background-color: #fff;
  border-radius: 50%;
  color: #000;
  width: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 20px;
}
nav input {
  padding: 10px;
  width: 100%;
  /* border: none; */
}
ul {
  list-style-type: none;
}
.btn {
  width: 100%;
  max-width: 1300px;
  margin: auto;
  padding: 10px;
  border-bottom: 1px solid #fff;
}
.btn {
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.btn ul {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  border-left: 1px solid #fff;
  border-right: 1px solid #fff;
}

.btn button {
  padding: 10px 20px;
  background-color: #d01418;
  color: #fff;
  border: none;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  gap: 10px;
}
.btn button:hover {
  cursor: pointer;
  background-color: #c00a0d;
}
.btn ul li {
  font-size: 1.5rem;
  padding: 10px 20px;
  transition: all 0.3s ease;
}
.btn ul li:hover {
  color: #d01418;
  cursor: pointer;
  text-decoration: underline;
}
.btn > :last-child {
  font-size: 1.5rem;
}
.categories {
  width: 100%;
  max-width: 1300px;
  margin: auto;
  position: relative;

  transition: all 0.5s ease;
}
.categories ul {
  position: absolute;
  z-index: 2;
  top: 15px;
  width: 100%;
  padding: 10px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: flex-start;
  align-items: center;
  background-color: #fff;
  box-shadow: 0px 0px 5px 1px #c0bdbd;
  border-radius: 10px;
  transition: all 0.5s ease;
}
.categories ul li {
  padding: 10px;
  font-size: 1.1rem;
  display: flex;
  align-items: center;
  gap: 10px;
  color: #000;
}
.hide {
  transform: translateY(-100%);
  display: none;
  transition: all 0.5s ease;
}
.show {
  transform: translateY(0);
  display: block;
}
.categories ul li img {
  width: 20px;
}
.categories ul li:hover {
  cursor: pointer;
  color: #d01418;
  background-color: #f1f1f1;
}

main {
  width: 100%;
  max-width: 1300px;
  margin: 20px auto;
}
.slider {
  width: 100%;
  height: 500px;
  position: relative;
  overflow: hidden;
}
.slider article {
  width: 95%;
  height: 95%;
  margin: 10px 5px 10px 30px;
  border-radius: 10px;
  display: grid;
  grid-template-columns: 2fr 1fr;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  text-align: center;
  box-shadow: 0px 0px 5px 1px #c0bdbd;
}
.slider img {
  width: 100%;
}
.slider article h4 {
  font-size: 3rem;
  opacity: 0.8;
}
.slider article p {
  font-size: 1.1rem;
  opacity: 0.7;
  width: 70%;
  margin: 20px auto;
}
.nextSlide {
  /* width: 100%; */
  /* height: 100%; */
  position: absolute;
  transform: translateX(150%);
  /* transition: all 0.5s ease; */
  opacity: 0;
}
.lastSlide {
  /* width: 100%; */
  /* height: 100%; */
  position: absolute;
  transform: translateX(-150%);
  transition: all 0.5s ease;
  opacity: 0;
}
.activeSlide {
  /* width: 100%; */
  /* height: 100%; */
  position: absolute;
  transform: translateX(0);
  transition: all 0.5s ease;
}

.products {
  width: 100%;
  max-width: 1300px;
  margin: auto;
  padding: 10px;
  display: flex;
  /* grid-template-columns: 1fr 3fr; */
  justify-content: flex-start;
  /* overflow: hidden; */
  align-items: flex-start;
}
.left1 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  /* background-color: #f1f1f1; */
  margin-top: 100px;
}
.left1 > :first-child,
.left1 > :nth-child(3) {
  position: relative;
}

.left1 > :first-child div,
.left1 > :nth-child(3) div {
  width: 100%;
  position: absolute;
  top: 80%;
  text-align: center;
}
.left1 > :first-child div p,
.left1 > :nth-child(3) div p {
  opacity: 0.8;
}
.left1 > :first-child div h4,
.left1 > :nth-child(3) div h4 {
  font-size: 1.5rem;
  font-weight: 600;
  padding: 10px;
}
.left1 > :first-child div > :last-child,
.left1 > :nth-child(3) div > :last-child {
  color: #d01418;
  font-size: 1.2rem;
}
.left1 > :nth-child(3) div {
  top: 70%;
  color: #fff;
}
.left1 > :first-child div > :last-child:hover,
.left1 > :nth-child(3) div > :last-child:hover {
  color: #c00a0d;
  cursor: pointer;
}
.left1 > :nth-child(2) h3 {
  text-align: center;
  font-size: 2rem;
  border-bottom: 3px solid #d01418;
  padding: 10px;
  opacity: 0.8;
}
.left1 > :nth-child(2) article {
  padding: 10px;
  text-align: center;
  display: flex;
  justify-content: flex-start;
  gap: 10px;
  cursor: pointer;
  align-items: center;
  border-bottom: 1px solid #000;
}

.left1 > :nth-child(2) article img {
  width: 150px;
}
.left1 > :nth-child(2) article h4 {
  font-size: 1.2rem;
  opacity: 0.8;
  font-weight: 600;
}
.left1 > :nth-child(2) article > :nth-child(2) div {
  display: flex;
  justify-content: center;
  align-items: center;
}
.left1 > :nth-child(2) article > :nth-child(2) > :nth-child(2) {
  gap: 5px;
  color: gold;
}
.left1 > :nth-child(2) article > :nth-child(2) > :nth-child(3) {
  gap: 10px;
  opacity: 0.7;
}
.right1 {
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 20px;
  padding: 10px 20px;
  overflow: hidden;

  /* border-left: 1px solid #000; */
}
.right1 > :first-child {
  width: 50%;
  border-bottom: 1px solid #d01418;
  margin: auto;
  font-size: 2rem;
  opacity: 0.8;
  padding: 10px;
  text-align: center;
  margin-bottom: 30px;
  transition: all 0.5s ease;
  position: relative;
}
.right1 > :nth-child(2) {
  width: 100%;
  padding-bottom: 20px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  overflow-x: scroll;
  scroll-behavior: auto;
  overscroll-behavior-x: contain;
  scroll-snap-type: x mandatory;
}
/* .right1>:nth-child(2)::-webkit-scrollbar{
  display: none;
} */

.right1 > :nth-child(2)::-webkit-scrollbar {
  width: 100%;
  height: 3px;
}

/* Track */
.right1 > :nth-child(2)::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
.right1 > :nth-child(2)::-webkit-scrollbar-thumb {
  background: #d01418;
}

/* Handle on hover */
.right1 > :nth-child(2)::-webkit-scrollbar-thumb:hover {
  background: #c00a0d;
}

.topCategories > :first-child::-webkit-scrollbar {
  width: 80%;
  height: 3px;
}

/* Track */
.topCategories > :first-child::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
.topCategories > :first-child::-webkit-scrollbar-thumb {
  background: #d01418;
}

/* Handle on hover */
.topCategories > :first-child::-webkit-scrollbar-thumb:hover {
  background: #c00a0d;
}

.right1 > :nth-child(2) article {
  padding: 10px;
  text-align: center;
  border-right: 1px solid #000;
  transition: all 0.5s ease;
  block-size: 100%;
  scroll-snap-align: start;
  scroll-snap-stop: always;
}
.pslide {
  transition: all 0.5s ease;
}

.right1 > :nth-child(2) article img {
  width: 100%;
  min-width: 300px;
}
.right1 > :nth-child(2) article p {
  opacity: 0.8;
}
.right1 > :nth-child(2) article h4 {
  font-size: 1.2rem;
  opacity: 0.8;
  padding: 10px;
}

.right1 > :nth-child(2) article div {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.right1 article > :nth-child(4) p {
  color: gold;
  font-size: 1.5rem;
  gap: 5px;
}
.right1 .cart {
  padding: 10px;
  margin: 10px auto;
  border: none;
  background-color: #d01418;
  color: #fff;
}
.right1 .cart:hover {
  background-color: #c00a0d;
  cursor: pointer;
}
/* .right1>:nth-child(2)>:first-child,.right1>:nth-child(2)>:last-child,.topCategories>:first-child>:first-child,.topCategories>:first-child>:last-child{
  font-size: 3rem;
  position: absolute;
} */

