  left: 0;
}
.topCategories>:first-child>:first-child{
  left: 1%;
}
.right1>:nth-child(2)>:last-child,.topCategories>:first-child>:last-child{
  right: 0;
}
.right1>:nth-child(2)>:first-child:hover,.right1>:nth-child(2)>:last-child:hover,.topCategories>:first-child>:first-child:hover,.topCategories>:first-child>:last-child:hover{
  cursor: pointer;
  color: #d01418;
} */

.pCard,
.jwellery {
  margin-top: 50px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 30px;
  flex-wrap: wrap;
  text-transform: capitalize;
}
.pCard article,
.jwellery article {
  /* width: 100%; */
  position: relative;
  text-align: center;
}
.pCard img {
  width: 250px;
  border-radius: 5px;
  box-shadow: 0px 0px 5px 1px #f1f1f1;
}
.jwellery img {
  width: 400px;
  border-radius: 5px;
  box-shadow: 0px 0px 5px 1px #f1f1f1;
}
.pCard div {
  position: absolute;
  right: 5%;
  top: 30%;
  display: flex;
  flex-direction: column;
}
.jwellery div {
  position: absolute;
  top: 20%;
  right: 5%;
  color: #fff;
}
.pCard p,
.jwellery p {
  opacity: 0.8;
  font-size: 0.9rem;
}
.pCard h4 {
  font-size: 1.2rem;
  opacity: 0.8;
  padding: 10px 0;
}
.jwellery h4 {
  font-size: 1.5rem;
  padding: 10px 0;
}

.pCard div > :last-child {
  color: #d01418;
  font-size: 1.1rem;
}
.jwellery div > :last-child {
  padding: 10px 0;
  opacity: 1;
}
.pCard div > :last-child:hover,
.jwellery div > :last-child:hover {
  color: #c00a0d;
  cursor: pointer;
}
.right1 > :nth-child(5) {
  font-size: 2rem;
  width: 50%;
  padding: 10px;
  border-bottom: 1px solid #d01418;
  opacity: 0.8;
  margin: auto;
  margin-top: 50px;

  text-align: center;
}
.topCategories {
  width: 100%;
  padding-left: 30px;
}
.topCategories > :first-child {
  width: 100%;
  margin-top: 30px;
  /* padding-left: 100px; */
  padding: 10px;

  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
  overflow-x: scroll;
  scroll-behavior: auto;
  overscroll-behavior-x: contain;
  scroll-snap-type: x mandatory;
}
.cslide {
  scroll-snap-align: start;
}
.topCategories div article {
  /* background-color: #f1f1f1; */
  border-radius: 10px;
  padding: 10px;
  text-align: center;
  position: relative;
  overflow: hidden;
  transition: all 0.5s ease;
  box-shadow: 0px 0px 5px 1px #c0bdbd;
}
.topCategories div article::before {
  content: "";
  width: 100%;
  height: 100%;
  background-color: #c00a0d;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-150%);
  transition: all 0.5s ease;
  z-index: -1;
}

.topCategories div article p {
  text-transform: capitalize;
  font-size: 1.2rem;
  font-weight: 600;
  opacity: 0.8;
  padding: 10px;
}

.topCategories div article:hover::before {
  transform: translateX(0);
}
.topCategories div article:hover {
  cursor: pointer;
  color: #fff;
}

.end {
  width: 100%;
  margin: 30px 10px;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  flex-wrap: wrap;
}
.end article {
  padding: 10px;
  text-align: center;
  box-shadow: 0px 0px 5px 1px #c0bdbd;
  border-radius: 10px;
}
.end article svg {
  font-size: 2rem;
}
.end article h4 {
  font-size: 1.1rem;
  opacity: 0.7;
  font-weight: 600;
  padding: 10px;
}
.end article p {
  opacity: 0.7;
}

footer {
  width: 100%;
  background-color: #000;
  color: #fff;
}
footer > :first-child {
  width: 100%;
  max-width: 1300px;
  margin: auto;
  padding: 50px 10px;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 30px;
  flex-wrap: wrap;
}
footer > :first-child > :first-child {
  width: 100%;
}
footer > :first-child > :first-child h2 {
  font-size: 2rem;
  font-weight: 600;
  padding: 10px 0;
}
footer > :first-child > :first-child p {
  padding: 10px;
  width: 60%;
  opacity: 0.8;
}
.box {
  padding: 20px;
  height: 100%;
}
.box > :first-child {
  font-size: 1.5rem;
  padding: 10px 0;
}
.box ul li {
  padding: 10px;
  opacity: 0.8;
}
.box ul li:hover {
  cursor: pointer;
  opacity: 1;
}
.box p {
  opacity: 0.8;
  padding: 10px;
}

@media screen and (max-width: 1250px) {
  .right1 > :nth-child(2) article img {
    width: 100%;
    min-width: 250px;
  }
}
@media screen and (max-width: 1000px) {
  .btn {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .btn ul {
    display: none;
  }
  .right1 > :nth-child(2) article img {
    min-width: 200px;
    width: 100%;
  }
  .left1 > :first-child img {
    width: 200px;
  }
  .left1 > :first-child div {
    top: 70%;
  }
  .left1 > :nth-child(2) h3 {
    font-size: 1.5rem;
  }
  .left1 > :nth-child(2) article img {
    width: 100px;
  }
  .left1 > :nth-child(3) img {
    width: 200px;
  }
  .left1 > :nth-child(3) div {
    top: 60%;
  }
  .cslide img {
    width: 100px;
  }
  .topCategories {
    padding-left: 0;
  }
  .jwellery {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  .jwellery div {
    top: 35%;
  }
  .jwellery img {
    width: 100%;
  }
  .jwellery div > :nth-child(2),
  .jwellery div > :nth-child(3) {
    display: none;
  }
}

@media screen and (max-width: 768px) {
  .slider {
    height: 400px;
  }

  .slider article {
    width: 90%;
  }
  .slider article h4 {
    font-size: 2rem;
  }
  .right1 > :nth-child(5) {
    width: 70%;
  }
  .right1 > :nth-child(2) article img {
    min-width: 150px;
    /* width: 100%; */
  }
  .left1 > :first-child img {
    width: 150px;
  }
  .left1 > :first-child div {
    top: 50%;
  }

  /* .left1>:nth-child(2) article img {
  width: 100px;
} */
  .left1 > :nth-child(3) img {
    width: 150px;
  }
  .left1 > :nth-child(3) div {
    top: 60%;
  }
  .cslide img {
    width: 100px;
  }
  /* .jwellery{
  display: grid;
  grid-template-columns: 1fr 1fr;
 } */
  /* .jwellery div{
  top: 35%;
 } */
  /* .jwellery img {
  width: 100%;
 } */
  .pCard {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
  .pCard img {
    width: 100%;
  }
  .pCard div {
    top: 0;
  }
  .pCard div > :first-child {
    display: none;
  }
  .pCard div > :nth-child(2) {
    font-size: 1rem;
  }
  .pCard div > :last-child {
    font-size: 0.8rem;
  }
  .end {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  .end article {
    height: 100%;
  }
}

@media screen and (max-width: 550px) {
  .slider {
    height: 300px;
  }
  .slider article {
    grid-template-columns: 1fr 1fr;
  }
  .slider article img {
    width: 200px;
  }

  .slider article h4 {
    font-size: 1.1rem;
  }
  .slider article p {
    font-size: 0.8rem;
  }
  .left1 {
    display: none;
  }
  .right1 > :first-child {
    width: 100%;
  }

  .right1 > :nth-child(2) article {
    padding: 10px 30px;
    /* width: 100%; */
  }
  .right1 > :nth-child(2) article div {
    gap: 5px;
    /* width: 100%; */
  }
  .right1 > :nth-child(2) article img {
    min-width: 100px;
    /* width: 100%; */
  }
  .right1 > :nth-child(2) article h4 {
    font-size: 1rem;
    /* width: 100%; */
  }

  .pCard {
    grid-template-columns: 1fr 1fr;
  }
  .jwellery {
    grid-template-columns: 1fr;
  }
  .categories ul {
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (max-width: 450px) {
  nav {
    justify-content: space-between;
    grid-template-columns: 1fr 1fr;
    border-bottom: none;
  }
  nav input {
    order: 3;
    grid-column: 1/3;
  }
  nav > :nth-child(3) {
    justify-content: flex-end;
  }
  .btn {
    justify-content: center;
    border-bottom: none;
  }
  .btn p {
    display: none;
  }

  .slider article {
    grid-template-columns: 1fr;
    align-items: center;
  }
  .slider article img {
    width: 200px;
    margin: auto;
  }

  .slider article h4 {
    font-size: 1.5rem;
    width: 80%;
    margin: auto;
  }
  .slider article p {
    display: none;
  }
}
